

.imageContainer{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    flex-grow: 50%;
}

.face, .git, .twitt, .link{
    transition: transform 1s;
    flex-basis: 10%;
    margin: 10px;

}

.face img, .git img, .twitt img, .link img{
    max-width: 100%;
}
.face:hover, .twitt:hover{ 
    transform: scale(0,0);
}

.git:hover, .link:hover{
    transform: scale(1.3,1.3);
}
